import React from "react";
const Controls = ({ onIncrement, onDecrement }) => (
  <div className="Counter__controls">
    <button type="button" onClick={onDecrement}>
      уменьшить на один
    </button>
    <button type="button" onClick={onIncrement}>
      Увеличить на один
    </button>
  </div>
);
export default Controls;
